
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  margin-top: 60px;
}

.text-left {
  text-align: left;
}
.text-center {
  text-align: center;
}
.text-right {
  text-align: right;
}
.grid {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.container {
  width: 50%;
}
.input-scope {
  margin: 0.5em 0;
}
.input {
  margin: 0.1em 0;
  padding: 0.6em 0.2em;
  font-size: 1em;
  width: calc(100% - 0.5em);
  border-radius: 4px;
  border: 1px solid #a3a3a3;
}
.input-message {
  font-size: 13px;
}
.input:focus {
  outline: none;
  -webkit-box-shadow: 0 1px 2px #818181;
          box-shadow: 0 1px 2px #818181;
}
.input-error {
  border: 1px solid #ff6347
}
.btn {
  border: none;
  outline: none;
  padding: 0.7em;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
}
.btn-primary {
  background-color: #34a1eb;
  color: #ffffff;
}
.tomato {
  color: #ff6347
}
.primary {
  color: #34a1eb;
}
